import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import jsonp from 'jsonp';


class App extends React.Component {

	state = {
		events: [],
		token: '',
		data: []
	};
	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppAccessTokenReceived':
					console.log('Данные токена', e.detail.type, e.detail.data.scope); 
					this.setState({ token: e.detail.data.access_token})
					break;
				case 'VKWebAppCallAPIMethodResult':
					this.setState({ data: e.detail.data.response });
					break;
				case 'VKWebAppCallAPIMethodFailed':
					console.log('Провал клика', e);
					break;
				default:
					console.log('Дефолтный лог', e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetAuthToken", {"app_id": 7087022, "scope": "friends, photos"});
	}
	click = () => {
		// connect.send("VKWebAppCallAPIMethod", {
		// 	"method": "messages.getHistoryAttachments", 
		// 	"request_id": "32test", 
		// 	"params": {
		// 		"peer_id": "75479717",
		// 		"media_type": "photo",
		// 		 "v":"5.101", 
		// 		 "access_token": this.state.token
		// }});
		connect.send("VKWebAppCallAPIMethod", {
			"method": "friends.get", 
			"request_id": "32test", 
			"params": {
				"user_id": "75479717",
				"count": "10",
				 "v":"5.101", 
				 "access_token": this.state.token
		}});
	}

	render() {
		console.log('Ренден лог', this.state);
		return (
			<div>
				{this.state.token}
				<button onClick={this.click}>
					Click
				</button>
			</div>
		);
	}
}

export default App;
